import React, { Component } from "react"
import { Row, Col, Button, Menu, Dropdown, Icon } from "antd"
// import { Link } from "react-router-dom"
import { connect } from "react-redux"
import socket from "../transport/Connect"
import { setActiveNode } from "../actions/node"
import { setPath } from "../actions/path"

class Auth extends Component {
  constructor() {
    super()
    // this.state = {
    //   nodes: [],
    //   activeNode: null
    // }
  }

  componentDidMount() {
    // this.setState({
    //   nodes: JSON.parse(localStorage.getItem("nodes")),
    //   activeNode: JSON.parse(localStorage.getItem("node"))
    // })
  }

  selectNode(node) {
    // this.setState({
    //   activeNode: node.item.props.data
    // })
    this.props.setActiveNode(node.item.props.data)
    localStorage.setItem("node", JSON.stringify(node.item.props.data))
  }

  connectNode(path) {
    if (this.props.node.activeNode) {
      socket.start(this.props.node.activeNode.Domains[0])
    } else {
      socket.start(this.props.node.allNodes[0].Domains[0])
      this.props.setActiveNode(this.props.node.allNodes[0])
      localStorage.setItem("node", JSON.stringify(this.props.node.allNodes[0]))
    }
    this.props.setPath(path)
  }

  render() {
    const menu = (
      <Menu onClick={this.selectNode.bind(this)}>
        {
          this.props.node.allNodes ?
            this.props.node.allNodes.map((node, i) => {
              return (
                <Menu.Item key={i} data={node}>
                  {node.Name}
                </Menu.Item>
              )
            })
          : null
        }
      </Menu>
    )
    return (
      <Row
        type="flex"
        align="middle"
        justify="center"
        style={{ height: "100vh" }}
      >
        <Col>
          <div className="auth__type">
            <h1>Вход</h1>
            <h2>Выберите способ входа</h2>
            <Button type="secondary" onClick={this.connectNode.bind(this, 'by-phone')}>По номеру телефона</Button>
            <br />
            <Button type="secondary" onClick={this.connectNode.bind(this, 'by-email')}>По электронной почте</Button>
            <br />
            <Button type="secondary">Через приложение</Button>
            <br />
            <br />
            <Button type="link" onClick={this.connectNode.bind(this, 'registration')}>Регистрация</Button>
            <br />
            <div className="auth__type_server">
              <span>Сервер: </span>
              <Dropdown overlay={menu}>
                <span className="ant-dropdown-link">
                  {localStorage.getItem("node")
                    ? JSON.parse(localStorage.getItem("node")).Name
                    : "Автоматически"}
                  <Icon type="down" />
                </span>
              </Dropdown>
            </div>
          </div>
        </Col>
      </Row>
    )
  }
}

function mapStateToProps(state) {
  return {
    node: state.node
  }
}

const mapDispatchToProps = dispatch => {
  return {
    setActiveNode: node => dispatch(setActiveNode(node)),
    setPath: path => dispatch(setPath(path))
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(Auth)
