import React, { useEffect } from "react"
import { connect } from "react-redux"
import { BrowserRouter as Router, Route, Redirect } from "react-router-dom"
import { Spin } from "antd"
import {
  dialogsReceived,
  messagesReceived,
  openDialog
} from "./actions/dialogs"
import { userReceived } from "./actions/user"
import { setAppFocus } from "./actions/ui"
import Auth from "./components/Auth"
import Registration from "./components/Registration"
import Verify from "./components/Verify"
import MainContainer from "./components/MainContainer"
import socket from "./transport/Connect"
import Connect from "./transport/Socket"
import { setAuth } from "./actions/auth"
import { setPath } from "./actions/path"
import { setNodes, setActiveNode } from "./actions/node"

const licens = new WebSocket("wss://testlic-1.ymess.org:5005")

function App(props) {
    // Устанавливаем флаг для отмечания сообщения прочитанными если вкладка не активна
  window.onfocus = () => {
    props.setAppFocus(true)
  }
  window.onblur = () => {
    props.setAppFocus(false)
  }

  useEffect(() => {
    if (props.auth === "AUTH_SUCCESS") {
      // запрос диалогов
      const getDialogs = {
        NavigationMessageTime: null,
        RequestId: socket.makeId(),
        RequestType: 12,
        Type: 0
      }

      socket.send(getDialogs)
    }
  }, [props.auth])

  useEffect(() => {
    licens.onopen = () => {
      // console.log("Соединение с лицензиаром установлено")
      const getNodesObj = {
        RequestId: socket.makeId(),
        RequestType: 1,
        Type: 6
      }

      licens.send(JSON.stringify(getNodesObj))
    }

    licens.onmessage = event => {
      const response = socket.blobToJson(event.data)
      if (response.ResponseType === 2) {
        props.setNodes(response.Nodes)
        // устанавливаем первую ноду по умолчанию если выбран режим "автоматически"
        if (!localStorage.getItem("node")) {
          // localStorage.setItem("node", JSON.stringify(response.Nodes[0]))
          // props.setActiveNode(response.Nodes[0])
        } else {
          props.setActiveNode(JSON.parse(localStorage.getItem("node")))
        }
      }
    }

    const node = JSON.parse(localStorage.getItem("node"))
    if (node) {
      props.setActiveNode(node)
    }

    if (localStorage.getItem("AccessToken")) {
      let login
      if (localStorage.getItem("User")) {
        login = {
          Token: {
            UserId: JSON.parse(localStorage.getItem("User")).Id,
            AccessToken: localStorage.getItem("AccessToken"),
            RefreshToken: localStorage.getItem("RefreshToken")
          },
          LoginType: 0,
          UidType: 1,
          RequestId: socket.makeId(),
          RequestType: 33,
          Type: 0
        }
      } else if (localStorage.getItem("Phone")) {
        login = {
          Uid: localStorage.getItem("Phone"),
          Token: {
            AccessToken: localStorage.getItem("AccessToken"),
            RefreshToken: localStorage.getItem("RefreshToken")
          },
          LoginType: 0,
          UidType: 0,
          RequestId: socket.makeId(),
          RequestType: 33,
          Type: 0
        }
      } else if (localStorage.getItem("Email")) {
        console.log("По почте")
      }

      socket.start(node.Domains[0])
      socket.wsOnConnect = () => {
        socket.send(login)
        // if(socket.send){
        // }
      }
    } else {
      props.setAuth("NO_AUTH")
    }
  }, [])

  return (
    <>
      <Connect />
      {
        props.auth === "CHECK_AUTH" ? 
          <div className="preloader">
            <Spin size="large" />
          </div>
        : props.auth === "AUTH_SUCCESS" ? 
            <MainContainer/>
        : props.path === "by-phone" || props.path === "by-email" ? 
          <Verify/>
        : props.path === "registration" ? 
          <Registration/> 
        : <Auth/>
      }


      {/* <Router>
        <Route
          path="/"
          render={
            // eslint-disable-next-line no-nested-ternary
            props.auth === "CHECK_AUTH"
              ? () => (
                  <div className="preloader">
                    <Spin size="large" />
                  </div>
                )
              : props.auth === "AUTH_SUCCESS"
              ? () => (
                  <>
                    <Route path="/" component={MainContainer} />
                  </>
                )
              : () => <Redirect to="/auth" />
          }
        />
        <Route path="/auth/by-phone" component={Verify} />
        <Route path="/auth/by-email" component={Verify} />
        <Route exact path="/auth" component={Auth} />
        <Route exact path="/registration" component={Registration} />
      </Router> */}
    </>
  )
}

function mapStateToProps(state) {
  return {
    dialogs: state.dialogs,
    users: state.users,
    auth: state.auth,
    path: state.path.currentPath
  }
}

const mapDispatchToProps = dispatch => {
  return {
    dialogsReceived: dialogs => dispatch(dialogsReceived(dialogs)),
    openDialog: dialogId => dispatch(openDialog(dialogId)),
    userReceived: user => dispatch(userReceived(user)),
    messagesReceived: messages => dispatch(messagesReceived(messages)),
    setAuth: auth => dispatch(setAuth(auth)),
    setNodes: nodes => dispatch(setNodes(nodes)),
    setActiveNode: node => dispatch(setActiveNode(node)),
    setPath: path => dispatch(setPath(path)),
    setAppFocus: data => dispatch(setAppFocus(data))
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(App)
