/* eslint-disable */
const defaultState = {
  prevPath: '',
  currentPath: ''
}

export function path(state = defaultState, action) {
  if(action.type === 'SET_PATH') {
    let newState = {...state}
    newState.prevPath = newState.path
    newState.currentPath = action.path
    return newState
  } else if(action.type === 'PREV_PATH') {
    let newState = {...state}
    newState.currentPath = newState.prevPath
    return newState
  }
  return state
}
