/* eslint-disable */
const defaulState = {
  activeNode: null,
  allNodes: null
}

export function node(state = defaulState, action) {
  if(action.type === 'SET_NODE') {
    let newState = {...state}
    newState.activeNode = action.node
    return newState
  } else if(action.type === 'SET_NODES') {
    let newState = {...state}
    newState.allNodes = action.nodes
    return newState
  }
  return state
}
