/* eslint-disable */
import React, {useState, useEffect} from "react"
import { connect } from "react-redux"
import { Modal, Button, Input, Divider, Form, Typography } from "antd"
import { toggleModal } from "../actions/ui"
import Avatar from "../components/Avatar"
import socket from "../transport/Connect"

const { Title } = Typography

function ContactsGroupsModal(props) {
  const [chatName, setChatName] = useState(null)
  const [nameError, setNameError] = useState(false)
  const [groupOpen, setGroupOpen] = useState(false)
  const [creatingGroup, setCreatingGroup] = useState(false)
  const [usersIDs, setUsersIds] = useState([])
  const [showUsers, setShowUsers] = useState(false)

  const handleSetChatName = value => {
    setChatName(value)
    setNameError(false)
    if (value.length < 1) {
      setNameError(true)
    }
  }

  const showGroups = () => {
    return props.groups.map(group => {
      return (
        <div className="contact" key={group.GroupId} onClick={() => openGroup(group.GroupId)}>
          <div className="name">{group.Title}</div>
        </div>
      )
    })
  }

  const openGroup = groupId => {
    const openedGroup = props.groups.find(group => group.GroupId === groupId)
    if (openedGroup) {
      setGroupOpen(openedGroup)
    }
  }

  const createGroup = () => {
    if (!chatName) {
      setNameError(true)
    } else {
      const newGroup = {
        Group: {
          GroupId: groupOpen ? groupOpen.GroupId : null,
          Title: chatName,
          UsersId: usersIDs,
        },
        RequestId: socket.makeId(),
        RequestType: 56,
        Type: 0
      }
      
      socket.send(newGroup)

      props.toggleModal("contactsGroupsModal")
    }
  }

  const showUsersInGroup = () => {
    return props.contacts.map(user => {
      if(user.GroupsId.includes(groupOpen.GroupId)){
        let name = null
        if (user.ContactUser.NameFirst) {
          name = user.ContactUser.NameFirst
        }
        if (user.ContactUser.NameSecond) {
          name = name + ' ' + user.ContactUser.NameSecond
        }
        return (
          <div key={user.ContactId} className='contacts'>
            <Avatar user={user.ContactUser} />
            {name ? name : 'Имя скрыто'}
          </div>
        )
      }
    })
  }

  const addContactToChat = userId => {
    let users = [...usersIDs]
    if (usersIDs.includes(userId)) {
      let foudnUserIndex = users.indexOf(userId)
      users.splice(foudnUserIndex, 1)
      setUsersIds(users)
    } else {
      users.push(userId)
      setUsersIds(users)
    }
  }

  const showContacts = () => {
    return props.contacts.map(user => {
      if(user){
        let name = null
        if (user.ContactUser.NameFirst) {
          name = user.ContactUser.NameFirst
        }
        if (user.ContactUser.NameSecond) {
          name = name + ' ' + user.ContactUser.NameSecond
        }
        return (
          <div key={user.ContactId} className={usersIDs.includes(user.ContactUserId) ? 'contacts contacts-active' : 'contacts'} onClick={() => {addContactToChat(user.ContactUserId)}}>
            <Avatar user={user.ContactUser} />
            {name ? name : 'Имя скрыто'}
          </div>
        )
      }
    })
  }

  return (
    <Modal
      // visible={props.contactsGroupsModal}
      visible={true}
      onCancel={() => props.toggleModal("contactsGroupsModal")}
      title="Группы контактов"
      footer={null}
      maskClosable
      className="contacts create-chat-modal"
    >
      {
        creatingGroup ?
          <div className="content">
            <Form.Item
              validateStatus={nameError ? "error" : ''}
              help={nameError ? "Это поле обязательно для заполнения" : false}
            >
              <Input 
                placeholder="Название группы"
                onChange={(e) => handleSetChatName(e.target.value)}
              />
            </Form.Item>
            <Divider />
            <div></div>
            <Button type="normal" block icon="usergroup-add" onClick={() => setShowUsers(!showUsers)}>Добавить пользователей</Button>
            {
              showUsers ?
                <div className="wrap_contacts">
                  {showContacts()}
                </div>
              :
                null
            }
            <Divider />
            <Button type="primary" block onClick={() => createGroup()}>Создать группу</Button>
          </div>
        : groupOpen ?
          <div className="content">
            <Input defaultValue={chatName ? chatName : groupOpen.Title} onChange={(e) => handleSetChatName(e.target.value)} />
            {/* <Title level={4}>{groupOpen.Title}</Title> */}
            <div className="wrap_contacts">
              {showUsersInGroup()}
            </div>
            <Divider />
            <Button type="normal" block icon="usergroup-add" onClick={() => setShowUsers(!showUsers)}>Добавить пользователей</Button>
            {
              showUsers ?
                <div className="wrap_contacts">
                  {showContacts()}
                </div>
              :
                null
            }
            <Divider />
            <Button type="primary" block onClick={() => createGroup()}>Обновить группу</Button>
          </div>
        :
          <>
            {showGroups()}
            <div className="content">
              <Button type="primary" block onClick={() => setCreatingGroup(true)}>Добавить группу</Button>
            </div>
          </>
      }
    </Modal>
  )
}

function mapStateToProps(state) {
  return {
    contactsGroupsModal: state.ui.modals.contactsGroupsModal,
    users: state.users,
    contacts: state.contacts.users,
    contactsIDs: state.contacts.usersIDs,
    groups: state.contacts.groups
  }
}

const mapDispatchToProps = dispatch => {
  return {
    toggleModal: name => dispatch(toggleModal(name))
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(ContactsGroupsModal)