/* eslint-disable */
export function openProfile(dialog) {
  return {
    type: "OPEN_PROFILE",
    dialog
  }
}

export function toggleDrawer(name) {
  return {
    type: "TOGGLE_DRAWER",
    name
  }
}

export function toggleModal(name) {
  return {
    type: "TOGGLE_MODAL",
    name: name
  }
}

export function setAppFocus(data) {
  return {
    type: "SET_APP_FOCUS",
    data
  }
}
